/**
 * @type		component
 * @name		content-popup
 * @moudle		module-a
 * @update		2022-04-11
 */
@keyframes popup-active {
  0% {
    visibility: hidden;
    opacity: 0;
  }
  100% {
    visibility: visible;
    opacity: 1;
  }
}
@keyframes popup-inactive {
  0% {
    visibility: visible;
    opacity: 1;
  }
  100% {
    visibility: hidden;
    opacity: 0;
  }
}
html.active-content-popup body {
  overflow: hidden;
}

.content-popup.module-a {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: fixed;
  z-index: 60;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 48rem;
  visibility: hidden;
  opacity: 0;
  background-color: rgba(0, 0, 0, 0);
}
.content-popup.module-a:not(.enabled) {
  display: none;
}
.content-popup.module-a:not(.active) {
  animation-name: popup-inactive;
  animation-duration: 0.25s;
  animation-fill-mode: forwards;
  animation-timing-function: ease;
}
.content-popup.module-a.active {
  background-color: rgba(0, 0, 0, 0.2);
  animation-name: popup-active;
  animation-duration: 0.25s;
  animation-fill-mode: forwards;
  animation-timing-function: ease;
}
.content-popup.module-a .popup-page-body {
  display: flex;
  min-width: 280rem;
  position: relative;
  overflow: hidden;
  width: 100%;
  opacity: 1;
  visibility: visible;
  transition: visibility 0.25s ease, opacity 0.25s ease;
}
.content-popup.module-a .popup-page-body .popup-local {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  width: 100%;
  align-items: center;
}
.content-popup.module-a .popup-page-body .popup-local .popup-local-body {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}
.content-popup.module-a .popup-page-body .popup-local .popup-local-body::-webkit-scrollbar {
  width: 4rem;
  height: 4rem;
}
.content-popup.module-a .popup-page-body .popup-local .popup-local-body::-webkit-scrollbar-thumb {
  border-radius: 2rem;
  background-color: rgba(0, 0, 0, 0.1);
}
.content-popup.module-a .popup-page-body .popup-local .popup-local-body::-webkit-scrollbar-thumb:hover {
  background-color: rgba(0, 0, 0, 0.3);
}
.content-popup.module-a .popup-page-body .popup-local .popup-local-body::-webkit-scrollbar-thumb:active {
  background-color: rgba(0, 0, 0, 0.4);
}
.content-popup.module-a .popup-page-body .popup-local .popup-local-body .popup-content {
  flex: 1 1 100%;
}
.content-popup.module-a.style-a .popup-page-body {
  background-color: white;
  border-radius: 16rem;
  box-shadow: 0 0.25rem 0.5rem 0 rgba(0, 0, 0, 0.08), 0 0 0 0.0625rem rgba(0, 0, 0, 0.05);
}
.content-popup.module-a.style-a .popup-page-body .popup-local .popup-local-head {
  display: block;
  position: absolute;
  overflow: hidden;
  visibility: hidden;
  margin: -1px;
  padding: 0;
  width: 1px;
  height: 1px;
  line-height: 1px;
  font-size: 1px;
  border: 0;
  clip: rect(0 0 0 0);
  clip: rect(0, 0, 0, 0);
  pointer-events: none;
}
.content-popup.module-a.style-a .popup-page-body .popup-local .popup-local-body {
  padding: 0 16rem;
}
.content-popup.module-a.style-a .popup-page-body .popup-local .popup-local-body .popup-content {
  margin: 24rem 0;
}
.content-popup.module-a.style-a .btn.popup-close {
  display: block;
  flex-shrink: 0;
  width: 100%;
  height: 48rem;
  border-top: 1rem solid #e8eced;
  color: #99a2a5;
  font-size: 13.3333333333rem;
  line-height: 20rem;
  font-weight: 700;
}
.content-popup.module-a.style-a.small .popup-page-body {
  max-width: 320rem;
}

/**
 * @type		component
