}
html.laptop .image-popup.module-a .popup-page-body .popup-local::-webkit-scrollbar {
  width: 4rem;
  height: 4rem;
}
html.laptop .image-popup.module-a .popup-page-body .popup-local::-webkit-scrollbar-thumb {
  border-radius: 2rem;
  background-color: rgba(255, 255, 255, 0.4);
}
