}
html.laptop .image-popup.module-a .popup-page-body .popup-local::-webkit-scrollbar-thumb:active {
  background-color: rgba(255, 255, 255, 0.8);
}

.image-popup.module-a .popup-page-body .popup-local .popup-local-head {
  display: block;
  position: absolute;
  overflow: hidden;
  visibility: hidden;
  margin: -1px;
  padding: 0;
  width: 1px;
  height: 1px;
  line-height: 1px;
  font-size: 1px;
  border: 0;
  clip: rect(0 0 0 0);
  clip: rect(0, 0, 0, 0);
  pointer-events: none;
}
.image-popup.module-a .btn.popup-close {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 16rem;
  right: 20rem;
  width: 48rem;
  height: 48rem;
  background: rgba(0, 0, 0, 0.1);
  border-radius: 50%;
}
.image-popup.module-a .btn.popup-close svg {
  display: block;
  width: 32rem;
  height: 32rem;
  fill: #e8eced;
}

/**
 * @type		component
 * @name		toast-popup
 * @moudle		module-a
 * @update		2022-03-27
 */
@keyframes toast-popup-active {
  0% {
    transform: translate(0, 120%);
  }
  100% {
    transform: translate(0, 0);
  }
}
@keyframes toast-popup-inactive {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(0, 120%);
  }
}
.toast-popup.module-a.style-b {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  position: absolute;
  z-index: 50;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  outline: none;
  background-color: rgba(0, 0, 0, 0);
  transition: background-color 0.5s ease;
}
.toast-popup.module-a.style-b:not(.enabled) {
  display: none;
}
.toast-popup.module-a.style-b:not(.active) .popup-page-body {
  animation-name: toast-popup-inactive;
  animation-duration: 0.25s;
  animation-fill-mode: forwards;
  animation-timing-function: cubic-bezier(0.2, 0, 0, 1);
}
.toast-popup.module-a.style-b.active {
  background-color: rgba(0, 0, 0, 0.2);
}
.toast-popup.module-a.style-b.active .popup-page-body {
  animation-name: toast-popup-active;
  animation-duration: 0.25s;
  animation-fill-mode: forwards;
  animation-timing-function: cubic-bezier(0.2, 0, 0, 1);
}
.toast-popup.module-a.style-b .popup-page-body {
  flex: 0 0 100%;
  width: 100%;
}
.toast-popup.module-a.style-b .popup-page-body .popup-local {
  overflow: hidden;
  border-top-right-radius: 16rem;
  border-top-left-radius: 16rem;
  background-color: #fff;
}
.toast-popup.module-a.style-b .popup-page-body .popup-local .popup-local-head {
  display: block;
  position: absolute;
  overflow: hidden;
  visibility: hidden;
  margin: -1px;
  padding: 0;
  width: 1px;
  height: 1px;
  line-height: 1px;
  font-size: 1px;
  border: 0;
  clip: rect(0 0 0 0);
  clip: rect(0, 0, 0, 0);
  pointer-events: none;
}
.toast-popup.module-a.style-b .popup-page-body .popup-local .popup-local-body {
  margin: 56rem 0 24rem 0;
  padding-right: 20rem;
  padding-left: 20rem;
}
.toast-popup.module-a.style-b .popup-page-body .popup-local .btn.popup-close {
  position: absolute;
  top: 16rem;
  left: 50%;
  transform: translate(-50%, 0);
}
.toast-popup.module-a.style-b .popup-page-body .popup-local .btn.popup-close svg {
  fill: #99a2a5;
}

