html,
body {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  word-break: keep-all;
  font-size: 13.3333333333rem;
  font-family: Open Sans, Noto Sans KR, Apple SD Gothic Neo, Malgun Gothic, 맑은 고딕, dotum, 돋움, sans-serif;
  -webkit-text-size-adjust: none;
  overflow-y: scroll;
}

hr {
  display: none;
}

img {
  max-width: 100%;
}

/**
 * @type layout
 * @name global
 */
#wrap {
  display: flex;
  justify-content: center;
  overflow: hidden;
  padding: 0 16rem;
}
@media (min-width: 1024px) {
  #wrap {
    padding: 0 40rem;
  }
}

#page {
  display: flex;
  position: relative;
  width: 100%;
  max-width: 1600rem;
  flex-direction: column;
  max-width: 1920px;
  min-height: 100vh;
  box-sizing: border-box;
}

/* 2021-01-01 김상유 수정 : 내용 */
#skip {
  display: none;
}

/**
 * @type layout
 * @name page-head
 */
.page-head {
  display: block;
  position: absolute;
  overflow: hidden;
  visibility: hidden;
  margin: -1px;
  padding: 0;
  width: 1px;
  height: 1px;
  line-height: 1px;
  font-size: 1px;
  border: 0;
  clip: rect(0 0 0 0);
  clip: rect(0, 0, 0, 0);
  pointer-events: none;
}

/**
 * @type layout
 * @name page-body
 */
.page-body {
  flex-grow: 1;
  width: 100%;
}
.page-body .local-head {
  display: flex;
  align-items: center;
  position: relative;
  height: 72rem;
  background-color: #000;
}
.page-body .local-head::after {
  content: "";
  display: block;
  position: absolute;
  z-index: -1;
  top: 0;
  bottom: 0;
  left: calc(50% - 50vw);
  width: 100vw;
  background-color: inherit;
}
.page-body .local-head .local-title {
  margin: 0;
  color: #fff;
  line-height: 1.5;
  font-size: 24rem;
}
.page-body .local-head .local-title .local-name {
  display: block;
}
.page-body .content-head {
  display: flex;
  align-items: center;
  position: absolute;
  top: 0;
  right: 0;
  height: 72rem;
}
.page-body .content-head .content-title {
  margin: 0;
  line-height: 1.5;
  font-size: 16rem;
  font-weight: normal;
  color: #fff;
}
.page-body .content-body {
  margin: 48rem 0;
}